import React from "react";
import { useState } from "react";
import './Card.scss';


const Card = ({image, name, tagline, abv, info, ml}) => {
const [showInfo, setShowInfo] = useState(false);
const [showImg, setShowImg] = useState(true);
const [showTagline, setShowTagline] = useState(true);
const [showAbv, setShowAbv] = useState(true);

    const toggleInfo = () => {
        setShowInfo(!showInfo)
        setShowImg(!showImg)
        setShowTagline(!showTagline)
        setShowAbv(!showAbv)
    }

    return (
        <div className="beerCard">
            {showImg && <img className="beerCard__img"src={image} alt=""/>}
             <h3 className="beerCard__title">{name}</h3>
            {showAbv && <p className="beerCard__abv">ABV {abv}%</p>}
            {showTagline &&<p className="beerCard__tagline">{tagline}</p>}
            <div className={showInfo ? "moreInfoOff" : "moreInfo"} onClick={toggleInfo}>
                {showInfo ? "Back" : "More Info"}
            </div>
            {showInfo  && <div className="beerCard__info">{info}</div>}
        </div>
    )
}


export default Card;